import { Avatar, Row, Col, Spin, Upload, Form, Input, Select, DatePicker, Button, message } from 'antd';
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import { getBase64 } from 'utils/image';
import { API_URL } from 'constant/apiUrl';
import Cookie from 'js-cookie';
import moment from 'moment';


// APIs
import { updateProfile } from 'services/user/user.service';
import { changePasswordAPI } from 'services/auth/auth.service';

const { Item, useForm } = Form;

const ProfilePage = props => {
  const { user } = props;
  const token = Cookie.get('EMR_token');

  const [loading, setLoading] = useState(false);
  const [updateLoading, setUpdateLoading] = useState(false);
  const [changePasswordLoading, setChangePasswordLoading] = useState(false);
  const [imageUrl, setImageUrl] = useState(user?.avatar);

  const [changePasswordForm] = useForm();

  useEffect(() => {
    if (user && user.avatar) {
      setImageUrl(user.avatar);
    }
  }, [user]);

  const uploadButton = (
    <div>
      {loading ? <LoadingOutlined /> : <PlusOutlined />}
      <div style={{ marginTop: 8 }}>Upload</div>
    </div>
  )

  function handleChange(info) {
    if (info.file.status === 'uploading') {
      setLoading(true);
      return;
    }
    if (info.file.status === 'done') {
      setImageUrl(info.file.response.avatar);
      setLoading(false);
    }
  };

  async function handleSaveProfile(values) {
    if (moment.isMoment(values.DOB)) {
      values.DOB = values.DOB.format('YYYY-MM-DD');
    }

    try {
      setUpdateLoading(true);
      await updateProfile(values);
    } catch (error) {
      console.log(error);
    } finally {
      setUpdateLoading(false);
    }
  }

  async function handleChangePassword(values) {
    try {
      setChangePasswordLoading(true);
      await changePasswordAPI(values);
      message.success("i mt khu thnh cng");
      changePasswordForm.resetFields();
    } catch (error) {
      console.log(error);
    } finally {
      setChangePasswordLoading(false);
    }
  }

  return !user ? <Spin spinning={true} /> : (
    <>
      <Row gutter={60}>
        <Col flex="0 0 50%">
          <h3 className="text-xl mb-3">H s c nhn</h3>
          <Upload
            name="image"
            action={`${API_URL}/user/profile/avatar/`}
            headers={{ Authorization: `Bearer ${token}` }}
            method="PATCH"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}

            onChange={handleChange} >
            {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
          </Upload>

          <Spin spinning={updateLoading}>
            <Form layout="vertical" onFinish={handleSaveProfile}>
              <Row gutter={15}>
                <Col flex="0 0 50%">
                  <Item label="H" name="first_name" initialValue={user.first_name}>
                    <Input />
                  </Item>
                </Col>
                <Col flex="0 0 50%">
                  <Item label="Tn" name="last_name" initialValue={user.last_name}>
                    <Input />
                  </Item>
                </Col>
              </Row>
              <Row gutter={15}>
                <Col flex="0 0 50%">
                  <Item label="Gii tnh" name="gender" initialValue={user.gender}>
                    <Select>
                      <Select.Option value="Nam">Nam</Select.Option>
                      <Select.Option value="N">N</Select.Option>
                      <Select.Option value="Khc">Khc</Select.Option>
                    </Select>
                  </Item>
                </Col>
                <Col flex="0 0 50%">
                  <Item label="Ngy sinh" name="DOB" initialValue={moment(user.DOB)}>
                    <DatePicker style={{ width: '100%' }} format="DD/MM/YYYY" />
                  </Item>
                </Col>
              </Row>
              <Row gutter={15}>
                <Col flex="0 0 50%">
                  <Item label="a ch email">
                    <Input value={user.email} readOnly />
                  </Item>
                </Col>
                <Col flex="0 0 50%">
                  <Item label="in thoi" name="phone" initialValue={user.phone}>
                    <Input />
                  </Item>
                </Col>
              </Row>
              <Row gutter={15}>
                <Col flex="0 0 50%">
                  <Item label="Ngh nghip" name="job" initialValue={user.job}>
                    <Input />
                  </Item>
                </Col>
                <Col flex="0 0 50%">
                  <Item label="Ni lm vic" name="workplace" initialValue={user.workplace}>
                    <Input />
                  </Item>
                </Col>
              </Row>
              <Row gutter={15}>
                <Col flex="0 0 50%">
                  <Item label="Dn tc" name="ethnicity" initialValue={user.ethnicity}>
                    <Input />
                  </Item>
                </Col>
                <Col flex="0 0 50%">
                  <Item label="Ngoi kiu" name="expatriate" initialValue={user.expatriate}>
                    <Input />
                  </Item>
                </Col>
              </Row>

              <Form.Item label="H v tn ngi nh" name="family_member_name" initialValue={user.family_member_name}>
                <Input />
              </Form.Item>

              <Form.Item label="a ch" name="address" initialValue={user.address}>
                <Input.TextArea autoSize={{ minRows: 2, maxRows: 4 }} />
              </Form.Item>

              <Form.Item label="a ch ngi nh" name="family_member_address" initialValue={user.family_member_address}>
                <Input.TextArea autoSize={{ minRows: 2, maxRows: 4 }} />
              </Form.Item>

              <Button type="primary" htmlType="submit">Cp nht</Button>
            </Form>
          </Spin>

        </Col>
        <Col flex="0 0 50%">
          <h3 className="text-xl mb-3">i mt khu</h3>

          <Spin spinning={changePasswordLoading}>
            <Form layout="vertical" onFinish={handleChangePassword} autoComplete="new-password" form={changePasswordForm}>
              <Item label="Mt khu c" name="old_password">
                <Input.Password />
              </Item>

              <Item label="Mt khu mi" name="new_password">
                <Input.Password />
              </Item>

              <Item label="Nhp li mt khu mi" name="confirm_password">
                <Input.Password />
              </Item>

              <Button type="primary" htmlType="submit">i mt khu</Button>
            </Form>
          </Spin>

        </Col>
      </Row>

    </>
  )
}

const mapStateToProps = state => ({
  user: state.userState.user
})

export default connect(mapStateToProps)(ProfilePage);